import { createSlice } from "@reduxjs/toolkit";

export enum StackEnum {
  HTML = "HTML",
  CSS = "CSS",
  JS = "JS",
  TS = "TS",
  React = "React",
  ReudxInfo = "ReudxInfo",
  RRD = "RRD",
  Axios = "Axios",
  Figma = "Figma",
  Sass = "Sass",
  Tailwind = "Tailwind",
}

export const toolkitTextSlice = createSlice({
  name: "textSlice",
  initialState: {
    textValue: "null",
    values: {
      [StackEnum.HTML]: "    ",
      [StackEnum.CSS]:
        " ,    ,        -   .",
      [StackEnum.JS]:
        "  ,       ,  .",
      [StackEnum.TS]:
        "  JS,       ,    ,  ,      ,         ,      TS    .          .",
      [StackEnum.React]:
        "  3-   (    ),    JS    ,       single page application.   React      ,       ,    .",
      [StackEnum.ReudxInfo]:
        "Redux - state ,    ,         props,   store         .    Redux,      Redux Toolkit,     .",
      [StackEnum.RRD]:
        " React Router Dom v.6,        .       Router Dom.",
      [StackEnum.Axios]:
        "JS ,          ,    promise-.           . ",
      [StackEnum.Figma]:
        "      ,            .",
      [StackEnum.Sass]:
        "     CSS,      .   ,     ,   .       .",
      [StackEnum.Tailwind]:
        "   CSS-    Angular,         .",
    },
  },
  reducers: {
    addText(state, code: { payload: { code: StackEnum } }) {
      state.textValue = state.values[code?.payload?.code];
    },
    removeText(state) {
      state.textValue = "";
    },
  },
});

export default toolkitTextSlice.reducer;
export const { addText, removeText } = toolkitTextSlice.actions;
